.Header{
    text-align: center;
}
.Atribuições{
    display: none;
}
.Galeria{
    display: flex;
    justify-content:center;
    flex-wrap: wrap;
    background-color: white;
    width: 94%;
    padding-left: 13px;
    padding-top: 5px;
    padding-bottom: 10px;
    margin: 0px auto;
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3);
}
.Item-Galeria{
    flex-basis: 32.7%; 
    width: 100%;
    margin-top: 14px;
    opacity: 0.85;
    cursor: pointer;
}
.Item-Galeria img{
  
    width: 95%;
    height: 100%;
    object-fit: cover;
    position: relative;
}
.Item-Galeria img:hover{
    transform: scale(1.1);
    
}
